.taskheader-container {
    margin: 0 50px;
}
.taskheader-wrapper {
    padding: 10px 40px;
    display: flex;
    align-items: center;
    box-shadow: 0px 0px 10px 0px rgb(0 0 0 / 10%);
    border-radius: 5px 5px 0 0;
    border-bottom: solid 1px rgba(0 0 0 / 5%);
}
.btn-add {
    margin: 10px;
    padding: 8px 30px;
    font-size: 1.2em;
    font-family: 'Poppins', sans-serif;
    background: #d3c0f9;
    border-radius: 5px;
    border: none;
    color: white;
    transition: all 0.3s ease;
    cursor: pointer;
}